class MobileMenu {
  constructor(toggleId, menuId) {
    this.toggleBtn = document.getElementById(toggleId);
    this.menu = document.getElementById(menuId);
    this.body = document.body;
    this.isOpen = false;
    this.scrollY = 0;

    if (!this.toggleBtn || !this.menu) {
      return; // Do nothing if essential elements are missing
    }

    this.init();
  }

  init() {
    this.toggleBtn.setAttribute('aria-expanded', 'false');
    this.toggleBtn.setAttribute('aria-controls', this.menu.id);

    this.toggleBtn.addEventListener('click', () => this.toggle());

    // Use event delegation for menu links
    this.menu.addEventListener('click', (e) => {
      if (e.target.closest('a')) {
        this.close();
      }
    });
  }

  toggle() {
    this.isOpen ? this.close() : this.open();
  }

  open() {
    if (this.isOpen) {
      return;
    }

    this.isOpen = true;
    this.body.classList.add('mobile-menu-open');
    this.menu.style.display = 'flex';
    this.toggleBtn.classList.add('is-open');
    this.toggleBtn.setAttribute('aria-expanded', 'true');
    this._disableScroll();
  }

  close() {
    if (!this.isOpen) {
      return;
    }

    this.isOpen = false;
    this.body.classList.remove('mobile-menu-open');
    this.menu.style.display = 'none';
    this.toggleBtn.classList.remove('is-open');
    this.toggleBtn.setAttribute('aria-expanded', 'false');
    this._enableScroll();
  }

  _disableScroll() {
    this.scrollY = window.scrollY;
    this.body.style.position = 'fixed';
    this.body.style.top = `-${this.scrollY}px`;
    this.body.style.width = '100%';
  }

  _enableScroll() {
    this.body.style.position = '';
    this.body.style.top = '';
    this.body.style.width = '';
    window.scrollTo(0, this.scrollY);
  }
}

document.addEventListener('DOMContentLoaded', () => {
  new MobileMenu('hamburger-toggle', 'mobile-menu');
});
