class TooltipManager {
  constructor(options = {}) {
    this.tooltips = new Map();
    this.currentVisibleTooltip = null;
    this.config = {
      boundaryPadding: options.boundaryPadding || 8,
    };
    this.init();
  }

  init() {
    this.setupEventListeners();
    this.registerInitialTooltips();
  }

  registerInitialTooltips() {
    document.querySelectorAll('[data-tooltip-trigger]').forEach((trigger) => {
      this.registerTooltip(trigger);
    });
  }

  setupEventListeners() {
    this.eventHandlers = {
      handleShow: this.handleTriggerEnter.bind(this),
      handleHide: this.handleTriggerLeave.bind(this),
      updatePosition: this.debounce(this.updateVisibleTooltipPosition.bind(this), 100),
    };

    document.addEventListener('mouseenter', this.eventHandlers.handleShow, true);
    document.addEventListener('mouseleave', this.eventHandlers.handleHide, true);
    document.addEventListener('focusin', this.eventHandlers.handleShow, true);
    document.addEventListener('focusout', this.eventHandlers.handleHide, true);
    window.addEventListener('resize', this.eventHandlers.updatePosition);
    window.addEventListener('scroll', this.eventHandlers.updatePosition, { passive: true });
  }

  handleTriggerEnter(e) {
    const trigger = this.findTooltipTrigger(e.target);
    if (trigger) {
      if (!this.tooltips.has(trigger)) {
        this.registerTooltip(trigger);
      }
      this.showTooltip(trigger);
    }
  }

  handleTriggerLeave(e) {
    const trigger = this.findTooltipTrigger(e.target);
    if (trigger && this.tooltips.has(trigger)) {
      this.hideTooltip(trigger);
    }
  }

  findTooltipTrigger(element) {
    let current = element;
    while (current && current !== document.body) {
      if (current.matches && current.matches('[data-tooltip-trigger]')) {
        return current;
      }
      current = current.parentElement;
    }
    return null;
  }

  destroy2() {
    document.removeEventListener('mouseenter', this.eventHandlers.handleShow, true);
    document.removeEventListener('mouseleave', this.eventHandlers.handleHide, true);
    document.removeEventListener('focusin', this.eventHandlers.handleShow, true);
    document.removeEventListener('focusout', this.eventHandlers.handleHide, true);
    window.removeEventListener('resize', this.eventHandlers.updatePosition);
    window.removeEventListener('scroll', this.eventHandlers.updatePosition, true);

    this.tooltips.forEach((tooltipData) => {
      if (tooltipData.tooltipElement && tooltipData.tooltipElement.parentElement) {
        tooltipData.tooltipElement.parentElement.removeChild(tooltipData.tooltipElement);
      }
    });

    this.tooltips.clear();
    this.currentVisibleTooltip = null;
  }

  registerTooltip(trigger) {
    const tooltipId = trigger.getAttribute('aria-describedby');
    if (!tooltipId) {
      return;
    }

    let tooltip = document.getElementById(tooltipId);
    if (!tooltip) {
      return;
    }

    document.body.appendChild(tooltip);

    this.tooltips.set(trigger, {
      element: tooltip,
      position: trigger.dataset.tooltipPosition || 'auto',
    });

    // Accessibility setup
    trigger.setAttribute('aria-haspopup', 'dialog');
    trigger.setAttribute('aria-expanded', 'false');
    tooltip.setAttribute('role', 'tooltip');
    tooltip.setAttribute('aria-hidden', 'true');

    // Initial positioning off-screen
    tooltip.style.position = 'fixed';
    tooltip.style.top = '-9999px';
    tooltip.style.left = '-9999px';
  }

  showTooltip(trigger) {
    if (this.currentVisibleTooltip) {
      this.hideTooltip(this.currentVisibleTooltip.trigger);
    }

    const { element, position } = this.tooltips.get(trigger);
    this.positionTooltip(trigger, element, position);

    element.classList.add('tooltip--visible');
    element.setAttribute('aria-hidden', 'false');
    trigger.setAttribute('aria-expanded', 'true');
    this.currentVisibleTooltip = { trigger, element };
  }

  hideTooltip(trigger) {
    const tooltipData = this.tooltips.get(trigger);
    if (!tooltipData) {
      return;
    }

    tooltipData.element.classList.remove('tooltip--visible');
    tooltipData.element.setAttribute('aria-hidden', 'true');
    trigger.setAttribute('aria-expanded', 'false');

    if (this.currentVisibleTooltip?.trigger === trigger) {
      this.currentVisibleTooltip = null;
    }
  }

  hideAllTooltips() {
    this.tooltips.forEach((_, trigger) => {
      this.hideTooltip(trigger);
    });
  }

  calculateBestPosition(position, triggerRect, tooltipRect, space, padding) {
    const positionStrategies = {
      top: () => ({
        top: triggerRect.top - tooltipRect.height - padding,
        left: triggerRect.left + triggerRect.width / 2 - tooltipRect.width / 2,
        currentPosition: 'top',
      }),

      bottom: () => ({
        top: triggerRect.bottom + padding,
        left: triggerRect.left + triggerRect.width / 2 - tooltipRect.width / 2,
        currentPosition: 'bottom',
      }),

      left: () => ({
        left: triggerRect.left - tooltipRect.width - padding,
        top: triggerRect.top + triggerRect.height / 2 - tooltipRect.height / 2,
        currentPosition: 'left',
      }),

      right: () => ({
        left: triggerRect.right + padding,
        top: triggerRect.top + triggerRect.height / 2 - tooltipRect.height / 2,
        currentPosition: 'right',
      }),

      x: () => {
        // Horizontal-only positioning
        const canPlaceRight = space.right > tooltipRect.width + padding;
        return {
          left: canPlaceRight
            ? triggerRect.right + padding
            : triggerRect.left - tooltipRect.width - padding,
          top: triggerRect.top + triggerRect.height / 2 - tooltipRect.height / 2,
          currentPosition: canPlaceRight ? 'right' : 'left',
        };
      },

      y: () => {
        // Vertical-only positioning
        const canPlaceBottom = space.bottom > tooltipRect.height + padding;
        return {
          top: canPlaceBottom
            ? triggerRect.bottom + padding
            : triggerRect.top - tooltipRect.height - padding,
          left: triggerRect.left + triggerRect.width / 2 - tooltipRect.width / 2,
          currentPosition: canPlaceBottom ? 'bottom' : 'top',
        };
      },

      auto: () => {
        // Smart positioning that checks all options
        const positions = [
          {
            // Bottom
            valid: space.bottom > tooltipRect.height + padding,
            top: triggerRect.bottom + padding,
            left: triggerRect.left + triggerRect.width / 2 - tooltipRect.width / 2,
            currentPosition: 'bottom',
            priority: space.bottom, // Prefer positions with most space
          },
          {
            // Top
            valid: space.top > tooltipRect.height + padding,
            top: triggerRect.top - tooltipRect.height - padding,
            left: triggerRect.left + triggerRect.width / 2 - tooltipRect.width / 2,
            currentPosition: 'top',
            priority: space.top,
          },
          {
            // Right
            valid: space.right > tooltipRect.width + padding,
            left: triggerRect.right + padding,
            top: triggerRect.top + triggerRect.height / 2 - tooltipRect.height / 2,
            currentPosition: 'right',
            priority: space.right,
          },
          {
            // Left
            valid: space.left > tooltipRect.width + padding,
            left: triggerRect.left - tooltipRect.width - padding,
            top: triggerRect.top + triggerRect.height / 2 - tooltipRect.height / 2,
            currentPosition: 'left',
            priority: space.left,
          },
        ];

        // Find all valid positions and pick the one with most available space
        const validPositions = positions.filter((p) => p.valid);
        if (validPositions.length === 0) {
          return positions[0];
        } // Fallback to first option

        return validPositions.reduce((best, current) =>
          current.priority > best.priority ? current : best,
        );
      },
    };

    // Handle specific position requests or fallback to auto
    const strategy = positionStrategies[position] || positionStrategies.auto;
    return strategy();
  }

  positionTooltip(trigger, tooltip, position = 'auto') {
    const triggerRect = trigger.getBoundingClientRect();
    const tooltipRect = tooltip.getBoundingClientRect();
    const padding = this.config.boundaryPadding;

    // Normal viewport positioning
    const viewport = {
      width: window.innerWidth,
      height: window.innerHeight,
    };

    const space = {
      top: triggerRect.top,
      bottom: viewport.height - triggerRect.bottom,
      left: triggerRect.left,
      right: viewport.width - triggerRect.right,
    };

    const positionResult = this.calculateBestPosition(
      position,
      triggerRect,
      tooltipRect,
      space,
      padding,
    );

    tooltip.style.position = 'fixed';
    tooltip.style.top = `${Math.max(padding, positionResult.top)}px`;
    tooltip.style.left = `${Math.max(padding, positionResult.left)}px`;
    this.updatePositionClass(tooltip, positionResult.currentPosition);
  }

  updatePositionClass(tooltip, position) {
    tooltip.classList.remove(
      'tooltip--position-top',
      'tooltip--position-bottom',
      'tooltip--position-left',
      'tooltip--position-right',
    );
    tooltip.classList.add(`tooltip--position-${position}`);
  }

  updateVisibleTooltipPosition() {
    if (this.currentVisibleTooltip) {
      const { trigger, element } = this.currentVisibleTooltip;
      const position = trigger.dataset.tooltipPosition || 'auto';
      this.positionTooltip(trigger, element, position);
    }
  }

  debounce(func, wait) {
    let timeout;
    return (...args) => {
      clearTimeout(timeout);
      timeout = setTimeout(() => func.apply(this, args), wait);
    };
  }

  destroy() {
    document.removeEventListener('mouseenter', this.boundHandleShow, true);
    document.removeEventListener('mouseleave', this.boundHandleHide, true);
    document.removeEventListener('focusin', this.boundHandleShow, true);
    document.removeEventListener('focusout', this.boundHandleHide, true);
    window.removeEventListener('resize', this.boundUpdatePosition);
    window.removeEventListener('scroll', this.boundUpdatePosition);

    this.hideAllTooltips();
    this.tooltips.clear();
  }
}

// Initialize automatically
document.addEventListener('DOMContentLoaded', () => {
  if (typeof window.tooltipManager === 'undefined') {
    window.tooltipManager = new TooltipManager();
  }
});
